import s from "./Supplies.module.css"
import img from "../SectionImage.jpg";
import Item from "./Item/Item";

export const Supplies = () => {

    const suppliesData = [
        {
            city: "-", description: [
                ' - 60 3/',
                ' - 60 3/',
                ' - 60 3/',
                ' - 60 3/',
                ' - 60 3/',
            ], img, features: '   -37 '
        },
        {
            city: "",
            description: [
                ' - 60 3/',
                ' - 60 3/',
                ' - 60 3/',
                ' - 60 3/',
                ' - 60 3/',
            ],
            img,
            features: '   ,      '
        },
        {
            city: "",
            description: [
                ' - 60 3/',
                ' - 60 3/',
                ' - 60 3/',
                ' - 60 3/',
                ' - 60 3/',
            ],
            img,
            features: '          95%'
        },
        {
            city: "", description: [
                ' - 60 3/',
                ' - 60 3/',
                ' - 60 3/',
                ' - 60 3/',
                ' - 60 3/',
            ], img, features: '   '
        },
    ]

    return (
        <div className={s.supplies} id="supplies">

            <div className={s.secondTitle}>
                <h2>  </h2>
            </div>

            <div className={s.suppliesContent}>
                        {suppliesData.map((s, i) => <Item key={i}
                                                                       city={s.city}
                                                                       description={s.description}
                                                                       img={s.img}
                                                                       features={s.features}/>
                        )}
            </div>

        </div>
    )
}